import React from "react";
import Formsy from "formsy-react";
import ReactIntl, {IntlMixin} from "react-intl";

Formsy.addValidationRule('isCEP', function (values, value) {
  return /^([\d]{5})\-?([\d]{3})$/.test(value);
});

var CEP = React.createClass({displayName: "CEP",
  mixins: [Formsy.Mixin, IntlMixin],

  getDefaultProps() {
    return {
      name: 'postalCode',
      validations: 'isCEP',
      required: true
    };
  },

  changeValue(event) {
    this.setValue(event.currentTarget.value);
  },

  render() {
    var className = this.props.className + ' form-group';
    var errorMessage;
    if (this.showRequired()) {
      className += ' required';
      errorMessage = this.props.requiredMessage || this.getIntlMessage('validation.required');
    }
    if (this.showError()) {
      className += ' error';
      errorMessage = this.props.errorMessage || this.getIntlMessage('validation.cep');
    }

    return (
      React.createElement("div", {className: className}, 
        React.createElement("label", {htmlFor: "cep"}, "CEP"), 
        React.createElement("input", {type: "text", 
          id: "cep", 
          className: "form-control", 
          onChange: this.changeValue, 
          tabIndex: this.props.tabIndex, 
          value: this.getValue()}), 
        React.createElement("p", {className: "help-block error"}, 
          errorMessage
        )
      )
    );
  }
});

export default CEP;



