import React from "react";
import ReactIntl, {IntlMixin, FormattedMessage} from "react-intl";
import "../styles/address-summary.less";

var AddressSummary = React.createClass({displayName: "AddressSummary",
  mixins: [ReactIntl.IntlMixin],

  onCheck() {
    if (this.props.onSelect) {
      this.props.onSelect(this.props.address);
    }
  },

  onEdit() {
    if (this.props.onEdit) {
      this.props.onEdit(this.props.address);
    }
  },

  render() {
    if (!this.props.address) {
      return;
    }
    var country = this.props.address.country;
    var countrySummary;
    try {
      countrySummary = require('./summaries/Summary' + country);
    }
    catch (e) {
      // Default summary
      countrySummary = require('./summaries/Summary');
    }

    return (
      React.createElement("div", {className: "address-summary"}, 
        React.createElement("div", {className: "selected"}, 
          React.createElement("label", null, 
            React.createElement("input", {type: "checkbox", 
                   checked: this.props.address.selected, 
                   onChange: this.onCheck}), 
             React.createElement(FormattedMessage, {
               message: this.getIntlMessage('selected')})
          )
        ), 
        countrySummary(this.props.address), 
        React.createElement("div", {className: "edit"}, 
          React.createElement("label", null, 
            React.createElement("button", {className: "btn btn-link", 
                    onClick: this.onEdit}, 
              React.createElement(FormattedMessage, {
                message: this.getIntlMessage('edit')})
            )
          )
        )
      )
    );
  }
});

export default AddressSummary;



