import React from "react";
import _ from "underscore";
import CEP from "../inputs/CEP";
import TextInput from "../inputs/Text";
import SelectInput from "../inputs/Select";

var states = ["AC","AL","AM","AP","BA","CE","DF","ES","GO","MA","MT","MS","MG","PA","PB","PR","PE","PI","RJ","RN","RO","RS","RR","SC","SE","SP","TO"];

export default function (address) {
  var className = 'input-group-' + address.country;
  return (
    React.createElement("div", {className: className}, 
      React.createElement("div", {className: "row"}, 
        React.createElement(CEP, {className: "col-sm-4", value: address.postalCode})
      ), 
      React.createElement("div", {className: "row"}, 
        React.createElement(TextInput, {className: "col-sm-8", 
          name: "street", 
          required: true, 
          value: address.street})
      ), 
      React.createElement("div", {className: "row"}, 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "number", 
          required: true, 
          value: address.number}), 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "complement", 
          required: true, 
          value: address.complement}), 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "neighborhood", 
          required: true, 
          value: address.neighborhood})
      ), 
      React.createElement("div", {className: "row"}, 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "city", 
          required: true, 
          value: address.city}), 
        React.createElement(SelectInput, {className: "col-sm-4", 
          name: "state", 
          options: states, 
          required: true, 
          value: address.state}), 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "country", 
          required: true, 
          tabIndex: "-1", 
          readOnly: "readonly", 
          value: address.country})
      ), 
      React.createElement("div", {className: "row"}, 
        React.createElement(TextInput, {className: "col-sm-4", 
          name: "receiverName", 
          required: true, 
          value: address.receiverName})
      )
    )
  );
}



