import React from "react";

export default function (address) {
  return (
    React.createElement("div", {className: "address-lines"}, 
      React.createElement("div", {className: "address-line-1"}, 
        React.createElement("p", null, address.street, ", ", address.number, ", ", address.complement, " - ", address.neighborhood)
      ), 
      React.createElement("div", {className: "address-line-2"}, 
        React.createElement("p", null, address.city, " - ", address.state, " - ", address.country)
      ), 
      React.createElement("div", {className: "address-line-3"}, 
        React.createElement("p", null, address.postalCode)
      )
    )
  );
}



